body{
    text-align: center;
    min-width: 480px;
    background-color: #003300;
}
.header{
    background-color: #eaffea;
    display: flex;
    height: 98vh;
    flex-direction: column;
    justify-content: center;
}
.header > img{
    align-self: center;
}
h1{
    font-family: 'Rajdhani', sans-serif;
    font-weight: 300;
    font-size: 3rem;
}
.navbar{
    position: fixed;
    background-color: #003300;
    align-content: center;
    width: 100%;
    top: 0px;
    left: 0px;
}
.navbar > ul{
    display: flex;
    margin: 10px;
    padding: 10px;
}
.navbar > li{
    display: block;
    list-style: none;
}
.link{
    background-color: #003300;
    color: #eaffea;
    padding: 10px;
}
.link:visited{
    background-color: #003300;
    color: #eaffea;
}
.link:hover{
    background-color: #eaeaea;
    color: #003300;
}
.rodape{
    position: fixed;
    background-color: #003300;
    color: #eaffea;
    align-content: center;
    width: 100%;
    bottom: 0px;
    left: 0px;
    padding: 10px;
    font-family: 'Rajdhani', sans-serif;
    font-weight: bolder;
}
#projetos{
    width: 100%;
    height: 100vh;
    background-color: #ddffdd;
}
